import Jump from 'jump.js'
var wow = require('./wow.js');

console.log(wow);

(function() {

	const jump = new Jump();

	var up = document.getElementById('up');
	var down = document.getElementById('down');
	

	up.onclick = function() {
		scrolling(document.documentElement.getBoundingClientRect().top)
	};

	down.onclick = function() {
		scrolling(document.querySelector('.services').getBoundingClientRect().top)
	};

	function scrolling(val) {
		
		jump.jump(val, {
			duration: 800,
			offset: 0,
			easing: (t, b, c, d) => {
				t /= d / 2
				if(t < 1) return c / 2 * t * t + b
					t--
				return -c / 2 * (t * (t - 2) - 1) + b
			}
		}) 
	};
	
	

})()





